<template>
  <div class="data-table-control-actions-table">
    <el-button-icon icon="mdi-plus" :disabled="!isMountTable" @click="addingElement"></el-button-icon>
    <el-button-icon icon="mdi-table-row-plus-after" :disabled="!isMountTable" @click="eventClickAddingTable"> </el-button-icon>
    <el-button-icon icon="mdi-folder-plus-outline" :disabled="!isMountTable" @click="addingGroup"> </el-button-icon>
    
    <dialog-full-page :is-dialog-name="` ${(typeElement == 'element') ? '' : ''}`" 
                      :is-dialog-show="isOpenDialog" 
                      @close-dialog="eventCloseDialog">
      <component :is="componentForm"
                 :guid="guid"
                 @event-action-accept="eventActionAccept"
                 @event-action-cancel="eventActionCancel"></component>
    </dialog-full-page>
  </div>
</template>

<script>
import { DataTableControlActions } from  './DataTableControlActions.js';

export default {
  name: 'DataTableControlActionsTable',
  mixins: [
    DataTableControlActions,
  ],
  props: {
    isMountTable: { type: Boolean, default: false },
  },
  methods: {
    async eventClickAddingTable() {
      console.log(this.elementFocused);
      let sendOption = {
        tableName: this.tableName,
        guid: this.guid,
        id: (this.elementFocused) ? this.elementFocused.id : -1,
      }
      // this.$store.commit('DataTable/DATA_STORE_ADDING_ELEMENT', sendOption);
      await this.$store.dispatch('DataTable/ADDING_INLINE_ELEMENT', sendOption)
        .then(() => {
          let index = this.$store.getters['DataTable/GET_ADDING_MODE']({ tableName: this.tableName, guid: this.guid }).index;
          let isGroup = this.$store.getters['DataTable/GET_DATA_GROUP_LEVEL']({ tableName: this.tableName, guid: this.guid });
          let addingElement = document.querySelectorAll(`.${this.guid} .body .body-row`)[(isGroup) ? index - 1 : index].querySelectorAll('.body-column')[0];
          let eventDblClick = new Event('dblclick', {bubbles: false});
          addingElement.focus();
          addingElement.dispatchEvent(eventDblClick);
        })
    },
    addingElement() {
      this.typeElement = 'element';
      this.eventOpenForm();
    },
    addingGroup() {
      this.typeElement = 'group';
      this.eventOpenForm();
    },
  },
}
</script>

<style lang="scss" scoped>
.data-table-control-actions-table {
  margin-left: -15px;
}
</style>